import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/pages/layout/layout'
import home from '@/pages/home/home'
import webgl from '@/pages/webgl/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'layout',
      component: layout,
      redirect: '/home',
      children: [
        {
          path: 'home',
          name: 'home',
          component: home
        },
        {
          path: 'webgl',
          name: 'webgl',
          component: webgl
        }
      ]
    }
  ]
})



