<template>
  <div>
    <div class="homeHeader">
      <router-link :to="index" tag="span" class="logo">web</router-link>
      <div class="headerNav" :class="{ listCon: !isBtnShow, animateList: isBtnShow }">
        <div
          v-for="(item, index) in navData"
          class="navList"
          :key="index"
          @click="navClick(index, item)"
          :class="{ activeNav: navActive === index }"
        >
          <span>{{item.name}}</span>
        </div>
      </div>
      <div class="headerShow" @click="showBtnFun" :class="{ animateBtn: isBtnShow }">
        <span class="iconfont showBtn" :class="{ contentShow: !isBtnShow }">&#xe6d5;</span>
        <span class="iconfont showBtn" :class="{ contentShow: isBtnShow }">&#xe608;</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'homeHeader',
  props: ['activeLink'],
  data () {
    return {
      index: '/',
      navData: [
        {
          name: '首页',
          path: 'home'
        },
        {
          name: 'webgl',
          path: 'webgl'
        }
      ],
      isBtnShow: false,
      navActive: 0
    }
  },
  methods: {
    showBtnFun () {
      this.isBtnShow = !this.isBtnShow
    },
    navClick (index, item) {
      this.navActive = index
      this.isBtnShow = false
      this.$router.push({name: item.path})
    }
  }
}
</script>
<style lang="sass" scoped>
  @import "~@/styles/variables"
  .homeHeader
    width: 100%
    font-size: 0
    @media #{$type} and ($protoMin: $valueBig)
      background: #2a395d
    .logo
      display: $display
      vertical-align: middle
      width: 1.5rem
      font-size: .2rem
      line-height: .35rem
      text-align: center
      background: $bgColor
      color: $sizeColor
      cursor: pointer
      @media #{$type} and ($protoMax: $valueSmall)
        width: 100%
        z-index: 11
        position: relative;
        line-height: .45rem
        background: $phoneTitleColor
    .headerNav
      display: $display
      vertical-align: middle
      width: calc(100% - 1.5rem)
      background: $bgColor
      .activeNav
        &:after, &:before
          border-bottom-color: $hoverColorActive !important
          border-right-color: $hoverColorActive !important
          border-top-color: $hoverColorActive !important
          border-left-color: $hoverColorActive !important
          width: 100% !important
          height: 100% !important
      @media #{$type} and ($protoMax: $valueSmall)
        width: 100%
        z-index: 10
        transition: all .5s
        transform: translateY(-100%)
        position: absolute
        top: .45rem
        left: 0
      .navList
        position: relative
        display: $display
        font-size: .14rem
        line-height: .35rem
        color: $sizeColor
        padding: 0 .2rem
        margin: .12rem .16rem
        box-sizing: border-box
        cursor: pointer
        @media #{$type} and ($protoMin: $valueBig)
          &:after, &:before
            content: ""
            display: block
            position: absolute
            width: .1rem
            height: .1rem
            border: .02rem solid
            transition: all 0.6s ease
            border-radius: .03rem
          &:after
            bottom: 0
            right: 0
            border-top-color: transparent
            border-left-color: transparent
            border-bottom-color: transparent
            border-right-color: transparent
          &:before
            top: 0
            left: 0
            border-bottom-color: transparent
            border-right-color: transparent
            border-top-color: transparent
            border-left-color: transparent
          &:hover:after, &:hover:before
            border-bottom-color: $hoverColorActive
            border-right-color: $hoverColorActive
            border-top-color: $hoverColorActive
            border-left-color: $hoverColorActive
            width: 100%
            height: 100%
        @media #{$type} and ($protoMax: $valueSmall)
          width: 100%
          border-bottom: 1px solid $hoverColor
          padding: .03rem .2rem
          margin: 0
          transition: all .3s
          &:hover
            background: $hoverColorActive
    .animateList
      @media #{$type} and ($protoMax: $valueSmall)
        transform: translateY(0)
    .headerShow
      display: none
      z-index: 12
      @media #{$type} and ($protoMax: $valueSmall)
        position: absolute
        top: .13rem
        left: .2rem
        cursor: pointer
        transition: all .3s
        display: block
        .showBtn
          display: none
          @media #{$type} and ($protoMax: $valueSmall)
            font-size: .18rem
            color: #fff
        .contentShow
          @media #{$type} and ($protoMax: $valueSmall)
            display: block
    .animateBtn
      transform: rotate(180deg)
</style>



